* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  width: 100vw;
  height: 100vh;
  position: relative;
  padding: 20px;
  display: flex;
}

.container {
  width: 500px;
  max-width: 100%;
  padding: 20px;
  margin: auto;
  background-color: #eee;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.task-app {
  width: 100%;
  height: 100%;
  position: relative;
  margin-top: 20px;
}

.add-form {
  display: flex;
  gap: 10px;
}

.add-form input {
  width: 100%;
  padding: 10px;
  outline: none;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size: 16px;
}

.add-form button {
  padding: 10px 15px;
  outline: none;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: rgb(32, 110, 183);
  color: white;
  cursor: pointer;
  transition: all 0.3s;
}

.add-form button:hover {
  background-color: rgba(32, 110, 183, 0.9) !important;
}

@media (max-width: 300px) {
  .add-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
}

.search-form {
  margin-top: 10px;
}

.search-form input {
  width: 100%;
  padding: 10px;
  outline: none;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size: 16px;
}

.tasks {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.tasks ul li {
  max-width: 100%;
  margin-top: 10px;
  padding: 10px;
  display: flex;
  align-items: center;
  background-color: white;
  border-radius: 10px;
  word-break: break-all;
}

.tasks ul li input[type="checkbox"] {
  accent-color: rgb(32, 110, 183);
  margin-right: 10px;
  margin-top: 3px;
  width: 20px;
  height: 20px;
}

.tasks ul li label {
  font-size: 20px;
}

.tasks ul li button {
  margin-left: auto;
  padding: 4px 8px;
  color: white;
  background-color: rgb(243, 63, 63);
  border: 1px solid rgb(243, 63, 63);
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  transition: all 0.3s;
}

.tasks ul li button:hover {
  background-color: rgb(243, 63, 63, 0.9) !important;
}

.no-tasks {
  font-size: 20px;
  font-weight: 500;
  text-align: center;
}

ul {
  list-style: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
